.HeaderTextWrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  margin-bottom: 50px;
  user-select: none;
}

.MainText {
  font-size: clamp(24px, 5vw, 48px);
  font-family: 'Press Start 2P', monospace;
  color: #00ffff;
  margin: 0;
}

.SubMainText {
  color: #0046FF;
  font-family: 'Press Start 2P', monospace;
  margin-left: 10px;
}
