.json-container {
  background-color: #000;
  color: #00ffff;
  font-family: 'Courier New', Courier, monospace;
  padding: 20px;
  border-radius: 8px;
  width: 70%;
  margin: 0 auto;
  white-space: pre-wrap;
  border: 0.5px solid #00ffff;
  box-shadow: 0 0 10px #00ffff;
  user-select: none;
}

.json-container .string {
  color: #ce9178;
  text-shadow: 0 0 5px #ce9178;
}

.json-container .number {
  color: #b5cea8;
  text-shadow: 0 0 5px #b5cea8;
}

.json-container .key {
  color: #9cdcfe;
  text-shadow: 0 0 5px #9cdcfe;
}
