.console {
  background-color: #000;
  color: #00ff00;
  font-family: 'Press Start 2P', monospace;
  padding: 20px;
  border-radius: 15px;
  width: 80%;
  margin: 80px auto 10px auto;
  max-width: 800px;
  min-height: 200px;
  box-shadow: 0 0 15px #00ff00;
  white-space: pre-wrap;
  user-select: none;
  text-shadow: 0 0 8px #00ff00;
}

.console-line {
  line-height: 1.5;
  animation: blinkIn 0.3s ease-in;
}

@keyframes blinkIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
