.MainSecondTextBlockWrap {
  width: 90%;
  overflow-x: auto;
  display: flex;
  margin-top: 30px;
  gap: 20px;
  margin-left: 50%;
  transform: translate(-50%);
  scroll-behavior: smooth;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 20px;
  border: 2px solid #00ffff;
  background-color: #000;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}

.MainSecondTextBlockWrap::-webkit-scrollbar {
  display: none;
}

.info {
  min-width: 200px;
  flex-shrink: 0;
  border-radius: 12px;
  padding: 20px;
  text-align: center;
  font-size: 18px;
  border: 0.5px solid #00ffff;
  color: #00ffff;
  font-family: 'Press Start 2P', monospace;
  background-color: #000;
  user-select: none;
}
